//mainReact
import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
//components
import BaseButton from "../../../../components/UI_kit/buttons_type/BaseButton";
//style
import classes from "./WelcomeBlock.module.css";
//other
import team from "./img/team.svg";

const WelcomeBlock = () => {
	useEffect(() => {
		AOS.init();
	}, []);

	return (
		<div className={classes.welcomeBox}>
			<div
				className={classes.descriptions}
				alt='work_team'
				data-aos='fade-right'
				data-aos-anchor='#example-anchor'
				data-aos-offset='500'
				data-aos-duration='500'
			>
				<h1>   </h1>
				<p>
					    
					,      .
				</p>
				<div className={classes.runButton}>
					<BaseButton to='/lessons' style={{ width: "209px" }}>
						 
					</BaseButton>
					<p>
						{" "}
						<span className={classes.mrPink}></span> 
						{" "}
						<span className={classes.mrPink}>
							 ?
						</span>
					</p>
				</div>
			</div>
			<img
				src={team}
				alt='work_team'
				data-aos='fade-left'
				data-aos-anchor='#example-anchor'
				data-aos-offset='500'
				data-aos-duration='500'
			/>
		</div>
	);
};

export default WelcomeBlock;
